
.el-carousel__container a {
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 5;
	left: 0;
	top: 0;
	text-decoration: none;
	color: #FFF;
}
.el-carousel__indicators {
	z-index: 10;
}
.el-carousel__container {
	height: 240px;
}
.el-carousel__item h3 {
  color: #fff;
  font-size: 16px;
  line-height: 420px;
  margin: 0;
}



