
.vue-sortable-nav {
  z-index: 7;
  position: fixed;
  right: 50px;
  min-width: 48px;
  width: auto;
  font-size: 12px;
  font-family: "Segoe UI", 'Microsoft Yahei', Helvetica, Arial;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.vue-sortable-nav .sortable-nav {
    position: relative;
    border: 1px solid #e5e9ef;
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;
    z-index: 9;
}
.vue-sortable-nav .sortable-nav.sortable-mode {
      cursor: move;
}
.vue-sortable-nav .sortable-nav li {
      text-align: center;
      background-color: #fff;
}
.vue-sortable-nav .sortable-nav.noDraging li:hover {
      background-color: #19ebe3;
      color: #fff;
}
.vue-sortable-nav .sortable-nav .pointer-block {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 32px;
      background-color: #19ebe3;
      transition: top .3s;
}
.vue-sortable-nav .sortable-nav .drag-block {
      position: absolute;
      width: 100%;
      height: 32px;
      color: #fff;
      z-index: 10;
      background-color: #19ebe3;
}
.vue-sortable-nav .sortable-nav .sortable-item {
      height: 32px;
      line-height: 32px;
}
.vue-sortable-nav .sortable-nav .sortable-item.active {
        transition: color .3s;
        color: #fff;
        background-color: #19ebe3;
}
.vue-sortable-nav .sortable-nav .sortable-item .sortable-item-name {
        position: relative;
        z-index: 9;
}
.vue-sortable-nav .sortable-nav .sortable-btn {
      border-top: 1px solid #e5e9ef;
      padding: 5px 0;
}
.vue-sortable-nav .sortable-nav .sortable-btn .sort-icon {
        margin-top: 2px;
        display: inline-block;
        height: 16px;
        width: 16px;
        background: url("data:image/png;base64,") no-repeat;
}
.vue-sortable-nav .sortable-nav .sortable-btn:hover .sort-icon {
        background: url("data:image/png;base64,") no-repeat;
}
.vue-sortable-nav .vue-sortable-btn {
    height: 40px;
    line-height: 40px;
    text-align: center;
    background-color: #f6f9fa;
    cursor: default;
}
.vue-sortable-nav .vue-backtotop-btn {
    height: 40px;
    line-height: 40px;
    width: 110%;
    position: relative;
    left: -5%;
    text-align: center;
    border-radius: 3px;
    background-color: #f6f9fa;
    border: 1px solid #e5e9ef;
    margin-top: 10px;
    font-size: 22px;
    z-index: 9;
    cursor: pointer;
}
.vue-sortable-nav .vue-backtotop-btn::before {
      content: '';
      position: absolute;
      top: -10px;
      left: 20%;
      width: 2px;
      height: 10px;
      background-color: #ccc;
}
.vue-sortable-nav .vue-backtotop-btn::after {
      content: '';
      position: absolute;
      top: -10px;
      left: 80%;
      width: 2px;
      height: 10px;
      background-color: #ccc;
}
.vue-sortable-nav .vue-backtotop-btn .backtotop-icon {
      width: 24px;
      height: 24px;
      display: inline-block;
      background: url("data:image/png;base64,") no-repeat;
}
.vue-sortable-nav .vue-backtotop-btn:hover {
      background-color: #19ebe3;
      color: #fff;
}
.vue-sortable-nav .vue-backtotop-btn:hover .backtotop-icon {
        background: url("data:image/png;base64,") no-repeat;
}
.vue-sortable-nav .tip {
    background: url(../assets/images/navTip.png) 0 0 no-repeat;
    position: absolute;
    left: -117px;
    top: 0;
    width: 117px;
    height: 333px;
    z-index: 8;
    transition: all .3s;
}
.vue-sortable-nav .custom-bg {
    position: absolute;
    top: -15px;
    left: -130px;
    height: 100%;
    width: 200px;
    padding-bottom: 35px;
    box-sizing: content-box;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 4px;
    z-index: 7;
    transition: all .3s;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
}
.fade-enter .tip, .fade-leave-active .tip {
  top: 50px;
  opacity: 0;
}
.fade-enter .custom-bg, .fade-leave-active .custom-bg {
  top: 150px;
  left: -70px;
  height: 100px;
  width: 100px;
  opacity: 0;
}



